

.ed-home-courses {
    position: relative;
    width: 100%;
    padding: 240px 0 70px;

    @include screen-xxl {
        padding: 300px 0 70px;
    }

    @include screen-xhl {
        padding: 50px 0;
    }

    .courses-banner {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 400px;
        overflow: hidden;
        pointer-events: none;

        @include screen-xhl {
            position: relative;
            margin-bottom: 50px;
        }

        img {
            position: absolute;
            top: 0;
            left: 50%;
            width: 1920px;
            max-width: 1920px;
            height: 100%;
            transform: translate3d(-50%, 0, 0);

            @include screen-xxl {
                left: 60%;
            }

            @include screen-xhl {
                left: 30%;
            }

            @include screen-sm {
                left: -10%;
            }
        }
    }

    .courses-title {
        width: calc(50% - 15px);
        margin-bottom: 50px;

        @include screen-xxl {
            width: calc(75% - 15px);
        }

        @include screen-xhl {
            width: 100%;
            margin-bottom: 30px;
        }

        @include screen-sm {
            margin-bottom: 30px;
        }
    }

    .courses-features {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 30px;
        row-gap: 30px;
        margin-bottom: 50px;

        @include screen-xhl {
            grid-template-columns: repeat(2, 1fr);
            column-gap: 20px;
        }

        @include screen-sm {
            grid-template-columns: repeat(1, 1fr);
        }

        &__item {
            display: flex;
            align-items: center;
        }

        .button {
            padding: 15px 60px;

            @include screen-xxl {
                padding: 15px 30px;
            }

            @include screen-xhl {
                width: 100%;
                padding: 15px 5px;
            }
        }

        .feature {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            @include screen-xhl {
                flex-direction: column;
                align-items: flex-start;
            }

            &__icon {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 80px;
                height: 80px;

                @include screen-xxl {
                    width: 40px;
                    height: 40px;
                }

                @include screen-xhl {
                    margin: 0 auto 10px;
                }

                svg {
                    width: 100%;
                    height: 100%;
                    stroke: $color-base;
                    transform: translate3d(0, 0, 0);
                }
            }

            &__title {
                display: flex;
                align-items: center;
                min-height: 120px;
                width: calc(100% - 100px);

                @include screen-xxl {
                    width: calc(100% - 60px);
                    min-height: 72px;
                    @include font(18px, 2);
                }

                @include screen-xhl {
                    width: 100%;
                    text-align: center;
                    min-height: 0px;
                }
            }
        }
    }
}